.popup__container {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 430px;
    box-sizing: border-box;
    padding: 32px 36px 36px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    background-color: #fff;
}

@media screen and (max-width: 560px) {
    .popup__container {
        max-width: 282px;
        padding: 23px 16px;
        text-align: center;
    }
}